import wzorE from '../../assets/img/imgWzorE.jpg';

export const WzorE = ()=>(
    <div className='wzorE'>
        <h4>Przykady:</h4>
    <div className='wzorE1Head'>
        <h4>Zadanie A - opis fotografii</h4>
        <p>Zadanie polega na opisaniu fotografii (naley opisa ludzii przedmioty,ktre si na niej znajduj, a nastpnie omwi sytuacj, ktr przedstawia).</p>
        <p>Przydatne zwroty i wyraenia</p>
        <div>
            <p>Na pierwszym planie, na drugim planie, obok, wok, w tle, w gbi</p>
            <p>Na fotografii (zdjciu, obrazku, ilustracji) widz...</p>
            <p>Myl,e... / Sdz, e... / Wydaje mi si, e...</p>
        </div>
    </div>
    <div className="zad1">
        <h5>Prosz opisa fotografi i przedstawion na niej sytuacj.</h5>
        <img src={wzorE} alt=''></img>
        <p>Na pierwszym planie widz ma dziewczynk, ktra jedzie ulic. Ma sze, moe siedem lat i uczy si jedzi na rolkach. Ma na sobie biao-niebiesk bluzk, biae spodenki, na kolanach i okciach rowe ochraniacze, a na gowie rowy kask. Na drugim planie jedzie na rowerze mczyzna w rednim wieku. To chybajej tata. Ma na sobie szare szorty i czarn koszulk.Jest umiechnity i cay czas obserwuje, co robi crka. Obok mczyzny, po lewejstronie,jedzie jeszcze jedna dziewczynka. Moeto starsza crka? Jest szczupa i ma dugie, jasne wosy. Ona dobrze jedzi na rowerze. Myl, e ta rodzina lubi uprawia sport. Dzi ju dugo jedzili i teraz s troch zmczeni,ale zadowoleni, poniewa modsza crka corazlepiej jedzi na rolkach. Sdz, e tata mobilizuje crki do tego, by aktywnie spdzay wolny czas. W ten sposb dbaj o kondycj oraz zdrowie.</p>
        <p>Wydaje mi si, e jest lato i s wakacje, poniewa pogodajest soneczna,jest ciepo, a drzewai trawa w tle s zielone. Sdz, e ta rodzina ciekawie zaplanowaa urlop. Fotografia bardzo misi podoba, bo przypomina moje wakacjei czas, ktry spdzam z rodzin. Chtnie znw pojechaabym na takie wakacje.</p>
    </div>

    <div className='wzorE2Head'>
        <h4>Zadanie B - monolog (wypowied na podany temat)</h4>
        <p>Naley umie wyrazi opini na podany temat, strecifilm lub ksik albo zrelacjonowa wydarzenie z przeszoci. Monolog najczciej dotyczy wasnych zainteresowa, dowiadcze i planw.</p>
        <p>Przydatne zwroty i wyraenia</p>
        <div>
            <p>Myl, e... Sdz, e... Uwaam, e... Moim zdaniem... Z jednej strony... Z drugiej strony... Z kolei...</p>
        </div>
    </div>
    <div className="zad2">
        <h5>Prosz wypowiedzie si na podany temat.</h5>
        <h5>Jak spdza Pan/Pani wolny czas? Jakie formy wypoczynku uwaa Pan/Paniza najlepsze? (monolog) </h5>
        <p>Kiedy mam wolnyczas, najchtniej spotykam si z przyjacimi i znajomymi. Zwykle idziemy razem do kawiarni albo do kina. Wsplnie decydujemy, co mamy ochot robi. Duo si miejemyi robimy wszystko, eby si dobrze bawi.</p>
        <p>Lubi te w wolnymczasie czyta ksiki. Nie ksiki powane,ale takie, ktre si dobrze czyta  przygodowealbo kryminay Agathy Christie.</p>
        <p>Niektrzy uwaaj, e tylko aktywny wypoczynek jest dobry, bo jest korzystny dla naszego zdrowiai zalecany jest wszystkim, w kadym wieku. Myl, e szczeglnie jest potrzebny tym, ktrzy cay dzie w pracy spdzaj przed komputerem.</p>
        <p>Z jednej strony wanejest, aby dba o swoje zdrowie,ale z drugiej strony przecie nie kady lubi chodzi na basen czy uprawia jaki sport. S osoby, ktre wol po caym dniu pracy usi przed telewizorem, obejrzejaki film, ewentualnie pj na krtki spacer do, parku. Z kolei modzi ludzie chtnie w wolnymczasie korzystaj z komputera  Internetu, gier komputerowych. Niejest dobrze, jeli przed komputerem spdzasi za duo czasu, wic rodzice powinni zadba, aby komputer nie by wczony cay wieczr i noc.</p>
        <p>Moim zdaniem najlepiej w wolnym czasie robi to, co si lubi. Sdz, e najwaniejsze jest, aby zrelaksowasi i odpocz. Wolny czas trzeba spdzi w jaki przyjemnydla siebie sposb.</p>
    </div>

    <div className='wzorE3Head'>
        <h4>Zadanie C - sytuacja komunikacyjna (dialog z egzaminatorem)</h4>
        <p>Trzecie zadanie to dialog z egzaminatorem. Tematyka zwizana jest z typowymi sytuacjami (oficjalnymii nieoficjalnymi) z ycia codziennego, np. rozmowatelefoniczna. Kiedyto konieczne, naley pamita o zachowaniu form grzecznociowych(np. pan/pani). Zdajcy najczciej wchodzi w rol: przyjaciela, znajomego, czonka rodziny, turysty, obcego, ucznia, nauczyciela, studenta, profesora, pracownika, pracodawcy, pacjenta czy klienta.</p>
        <p>Czas przed rozpoczciem wypowiedzi warto przeznaczy na szybkie zastanowienie si, o co naley zapyta oraz jak rol trzeba odegra  wszystkie te informacje zdajcy odnajdzie w poleceniu. Wane jest wic, aby uwanie przeczyta polecenie i zaplanowa, jak pokierowa rozmow, jakich uy argumentw. Nie warto pisa caego dialogu, poniewanie jestemy w stanie przewidzie reakcji naszego rozmwcy. Najlepiej wic przygotowa plan oglny.</p>
        <p>Niektre zwroty i wyraenia uywane w typowych sytuacjach komunikacyjnych</p>
        <div>
            <table>
                <thead>
                    <tr>
                        <td><b>Oficjalnie</b></td>
                        <td><b>Nieoficjalnie</b></td>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td colSpan={2}><b>Powitanie/poegnanie:</b></td>
                    </tr>
                    <tr>
                        <td>Dzie dobry/Dobry wieczr</td>
                        <td>Cze / Hej</td>
                    </tr>
                    <tr>
                        <td>Witam pana/pani</td>
                        <td>Witaj</td>
                    </tr>
                    <tr>
                        <td>Witam serdecznie</td>
                        <td>Mio ci widzie</td>
                    </tr>
                    <tr>
                        <td>Do zobaczenia / Do widzenia</td>
                        <td>Cze / Trzymajsi / Na razie</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Przedstawianiesiebie lub kogo:</b></td>
                    </tr>
                    <tr>
                        <td>Pozwoli pan/pani, e si przedstawi, nazywam si Jan Nowak.</td>
                        <td>Jestem Jan Nowak. Mam na imi Jan.</td>
                    </tr>
                    <tr>
                        <td>Przepraszam, chciaabym panu/pani przedstawi mojego ma...</td>
                        <td>Poznajcie si, to mj m...</td>
                    </tr>
                    <tr>
                        <td>Mam zaszczyt przedstawi pastwu...</td>
                        <td>To mj kolega...</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Pytania i odpowiedzi grzecznociowe:</b></td>
                    </tr>
                    <tr>
                        <td>Jak si pan/pani ma? Dzikuj, wszystko dobrze.</td>
                        <td>Jak si masz? wietnie!</td>
                    </tr>
                    <tr>
                        <td>Co u pastwa nowego? Dzikujemy, wszystko tak, jak byo.</td>
                        <td>Co u ciebie? Nic nowego.</td>
                    </tr>
                    <tr>
                        <td>Co u pana/pani sycha? Dzikuj, wszystko w porzdku.</td>
                        <td>Co sycha? Po staremu.</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Zwracaniesi do odbiorcy:</b></td>
                    </tr>
                    <tr>
                        <td>Prosz pana/pani.</td>
                        <td>Tomku! Kasiu! Suchaj! Wiesz co...</td>
                    </tr>
                    <tr>
                        <td>Przepraszam, czy mog przeszkodzi?</td>
                        <td>Mona na moment?</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Wprowadzanie pytania:</b></td>
                    </tr>
                    <tr>
                        <td>Prosz mi powiedzie...</td>
                        <td>Powiedz mi...</td>
                    </tr>
                    <tr>
                        <td>Czy moe mi pan/pani powiedzie...</td>
                        <td>Czy moesz mi powiedzie...</td>
                    </tr>
                    <tr>
                        <td>Prosz pana/pani, czy...</td>
                        <td>Suchaj, czy wiesz...</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Odpowiedzina pytania:</b></td>
                    </tr>
                    <tr>
                        <td>Przykro mi, ale nie wiem.</td>
                        <td>Nie wiem. / Nie mam pojcia.</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Przepraszanie:</b></td>
                    </tr>
                    <tr>
                        <td>Przepraszam pana, panie dyrektorze.</td>
                        <td>Bardzo przepraszam.</td>
                    </tr>
                    <tr>
                        <td>Przepraszam i obiecuj, e to si ju nigdy nie powtrzy.</td>
                        <td>Nie gniewaj si! Wybacz!</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Niezadowolenie:</b></td>
                    </tr>
                    <tr>
                        <td>Jestem niezadowolony/niezadowolona z...</td>
                        <td>Jestem zy/za! Jestem wcieky/wciekta!</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Propozycje i odpowiedzi:</b></td>
                    </tr>
                    <tr>
                        <td>Mam propozycj dla pastwa/pani/pana...</td>
                        <td>Masz ochot na...?</td>
                    </tr>
                    <tr>
                        <td>wietnie, znakomity pomyst!</td>
                        <td>Dlaczego nie! / No pewnie, e tak!</td>
                    </tr>
                    <tr>
                        <td>Niestety, dzi to niemoliwe. Czy mgtby to pan przeoy?</td>
                        <td>Chyba nie. / Nie mam ochoty. / Mowy nie ma.</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Gratulacje i yczenia:</b></td>
                    </tr>
                    <tr>
                        <td>Skadam panu/pani najserdeczniejsze gratulacje z okazji...</td>
                        <td>Moje gratulacje!</td>
                    </tr>
                    <tr>
                        <td>Z okazji imienin / urodzin ycz panu/pani spenienia wszystkich marze.</td>
                        <td>Zdrowia, szczcia, mioci...</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Zaproszenia i podzikowania:</b></td>
                    </tr>
                    <tr>
                        <td>Mam zaszczyt zaprosi pana/pani/pastwa...</td>
                        <td>Naprawd serdecznie was zapraszam...</td>
                    </tr>
                    <tr>
                        <td>Dzikujemy za zaproszenie.</td>
                        <td>Z przyjemnoci przyjdziemy!</td>
                    </tr>
                    <tr>
                        <td>Niestety nie moemy przyj z powodu...</td>
                        <td>Dzikujemy za zaproszenie, ale...</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Perswazja i argumentacja:</b></td>
                    </tr>
                    <tr>
                        <td>Powinien pan/powinna pani...</td>
                        <td>Powiniene/powinna...</td>
                    </tr>
                    <tr>
                        <td>Wydaje mi si, e powinien pan/powinna pani...</td>
                        <td>Sdz, e powiniene/powinna...</td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
    <div className="zad3">
        <h5>Prosz wykona nastpujce zadanie.</h5>
        <h5>Idzie Pan/Pani ulic ze swoj siostr i spotyka nauczycielk jzyka polskiego, ktra pomagaa Panu/Pani przygotowa si do egzaminu. Prosz rozpocz rozmow, przedstawi siostr i powiedzie o zdanym egzaminie.</h5>
        <p><b>Student: </b>Dzie dobry, czy poznaje mnie pani?</p>
        <p><b>Nauczycielka: </b>Oczywicie, e tak! Dzie dobry John! Jak mogabym zapomnie mojego najlepszego studenta?</p>
        <p><b>Student: </b>Dzikuj, chciabym pani przedstawi moj siostr Ann.</p>
        <p><b>Nauczycielka: </b>Mio mi pozna. John jest naprawd zdolnym studentem. Cou ciebie sycha, John?</p>
        <p><b>Student: </b>Wszystko w porzdku,a najlepsza wiadomojest taka, e zdaem egzamin!</p>
        <p><b>Nauczycielka: </b>Gratuluj! Wiedziaam, e nie moe by inaczej! Czy by trudny?</p>
        <p><b>Student: </b>Tak, bardzo, ale wszystko skoczyo si dobrze.</p>
        <p><b>Nauczycielka: </b>A czy wiesz, jak poszo Andreasowi z naszej grupy?</p>
        <p><b>Student: </b>Nie mam pojcia, nie kontaktowa si ze mn.</p>
        <p><b>Nauczycielka: </b>A jakie masz teraz plany?</p>
        <p><b>Student: </b>Przyjechaem z siostr na wakacje, eby jej pokaza Polsk, a potem wracam do Irlandii i zaczynam prac.</p>
        <p><b>Nauczycielka: </b>ycz wic powodzeniai pisz czasami do mnie.</p>
        <p><b>Student: </b>Oczywicie, obiecuj. A moe pjdziemy razem na kaw?</p>
        <p><b>Nauczycielka: </b>Bardzo chtnie,ale za p godziny mam zajcia..., moe innym razem.</p>
        <p><b>Student: </b>Dobrze.</p>
        <p><b>Nauczycielka: </b>Do widzenia.</p>
        <p><b>Student: </b>Do widzenia.</p>
    </div>
</div>
)