import { useState } from "react"
import { Modal, ModalBody, ModalHeader } from "reactstrap"

export const DescCatD=()=>{
    const[open, setOpen]=useState(true)
    return(
        <Modal isOpen={open} toggle={() => setOpen(false)}>
            <ModalHeader>
                Cz D 
                <button  type="button" className="btn-close" onClick={() => setOpen(false)}  ></button>
            </ModalHeader>
            <ModalBody>
            <div>
            <h5>Maksymalna liczba punktw  40</h5>
            <h5>Czas przeznaczony na wykonanie zada - 90 min</h5>
            <h5>Warunkiem zdania egzaminu jest uzyskanie pozytywnego wyniku (minimum 60%, czyli 24p.) z kadej czci egzaminu</h5>

            <h4>Oglny opis umitnoci</h4>
            <p>Zdajcy powinni opanowa relacje goska - litera, podstawowe zasady ortografii i interpunkcji oraz zasady kompozycji tekstw pisanych, ze zwrceniem uwagi na styl oficjalny i nieoficjalny, zgodnie z wymaganiami zawartymi w Standardach wymaga egzaminacyjnych.</p>
            
            <h4>Wskazwki dla przygotowujcych si do egzaminu</h4>
            {/* <h5>Budowa egzaminu ustnego i typy zada</h5> */}
            <p>Ta cz egzaminu polega na napisaniu dwch tekstw. Naley wybra jeden zestaw spord trzech i wykona obydwa polecenia (punkt a i b). Pierwsze zadanie to zredagowanie krtkiego tekstu o okrelonej formie (np. yczenia, pozdrowienia, ogoszenie). Warto przypomnie sobie, jak struktur charakteryzuje si konkretna forma, np. zaproszenie to nagwki, zwroty do adresata, daty czy dane adresowe. W drugim zadaniu naley napisa duszy tekst,np.list oficjalny lub nieoficjalny, opis, tekst argumentacyjny lub opowiadanie, podstawow form yciorysu lub podania.</p>
            <h5>Uwaga! Dugo napisanego tekstu powinna by zgodna z liczb sw podanych w poleceniu. W zadaniu <i>'a'</i> jest to najczciej 2030 sw, w zadaniu <i>'b'</i> 170180 sw.</h5>
            <p>Warto powici kilka minut na dokadne przemylenie wyboru zestawu.</p>
            <p>Przed rozpoczciem pisania dobrze jest sporzdzi ramowy plan do kadego z dwch tekstw. Powinien on zawiera wypunktowane informacje i argumenty. Aby nie traci czasu na liczenie wyrazw podczas egzaminu, mona wczeniej w domu wykona prb ilociow: zapisa odrcznie na arkuszu A4 okoo szeciu linijek, potem policzy, ile sw zawiera ten tekst, a nastpnie wyliczon liczb sw podzieli przez liczb linijek. Uzyskany w ten sposb wynik bdzie redni sw, ktre zwykle mieszcz si w jednej linijce. Mona wtedy na egzaminie liczy linijki, a nie sowa.</p>
            <p>Niektre osoby pierwsz wersj tekstu zapisuj w brudnopisie. Egzaminatorzy oceniaj jednak wycznie czystopis. Zatem naley tak rozplanowa czas, aby wystarczyo go jeszcze na przepisanie tekstu na arkusze do tego przeznaczone.</p>
            <p>Wypowied pisemna oceniana jest niezalenie przez dwie osoby. Wynik testu to suma punktw obu egzaminatorw. Oceniane s: wykonanie zadania(tre, dugo, forma, kompozycja), poprawno gramatyczna, sownictwo, styl, ortografia i interpunkcja.</p>
        </div>
            </ModalBody>
        </Modal>
        
    )
    
}