import { useState } from "react"
import { Modal, ModalBody, ModalHeader } from "reactstrap"

export const DescCatA=()=>{
    const[open, setOpen]=useState(true)
    return(
        <Modal isOpen={open} toggle={() => setOpen(false)}>
            <ModalHeader>
                Cz A 
                <button  type="button" className="btn-close" onClick={() => setOpen(false)}  ></button>
            </ModalHeader>
            <ModalBody>
            <div>
            <h5>Maksymalna liczba punktw  40</h5>
            <h5>Czas przeznaczony na wykonanie zada - 30 min</h5>
            <h5>Warunkiem zdania egzaminu jest uzyskanie pozytywnego wyniku (minimum 60%, czyli 24p.) z kadej czci egzaminu</h5>
            <h4>Oglny opis umitnoci</h4>
            <p>Zdajcy powinni rozumie pojedyncze wypowiedzi, wypowiedzi w ramach dialogu i rozmowy z udziaem kilku osb, dusze wypowiedzi monologowe (w tym informacje, prezentacje, ogoszenia i instrukcje) w zakresie wskazanym w Standardach wymaga egzaminacyjnych. Zdajcy powinni umie upewnisi, czy waciwie zrozumieli trei intencj wypowiedzi, oraz potrafi poprosi o powtrzenie, wyjanienie fragmentu wypowiedzi lub zapisanie niezrozumiaego sowa.</p>
            <h4>Wskazwki dla przygotowujcych si do egzaminu</h4>
            <p>Ta cz egzaminu skada si z 4 lub 5 zada. Zawarte tu teksty to krtkie komunikaty, fragmenty rozmw, nagrania programw radiowych i telewizyjnych oraz teksty preparowane na uytek egzaminu. Do kadego nagrania naley wykona zadanie. Uwaga! Zwykle nagrania odtwarzanes dwarazy, ale jedno  tylko jeden raz. Teksty do wysuchania prezentowane s z pyty CD lub kasety i niemoliwe jest zatrzymanie lub ponowne odtworzenie pyty (kasety). W zwizku z tym w tej czci egzaminu zadania musz by wykonane we wskazanej kolejnoci, a aktywno egzaminatora ogranicza si do rozdania testw oraz do wczenia nagrania. Dlatego wszystkie pytania i ewentualne wtpliwoci naley rozstrzyga tu po otrzymaniu testu  w czasie suchania nie jest to moliwe. Przed kadym nagraniem pojawia si moment ciszy. Naley go wykorzysta na przeczytanie zadania, eby wiedzie, ktre z usyszanych informacji bd wane. Natomiast podczas chwili przerwy przed drugim odtworzeniem tego samego tekstu naley zrobi korekt zapisanych ju sw (zda) i zastanowi si nad brakujcymi informacjami.</p>
        </div>
            </ModalBody>
        </Modal>
        
    )
    
}