import { useState } from "react"
import { Modal, ModalBody, ModalHeader } from "reactstrap"

export const DescCatE=()=>{
    const[open, setOpen]=useState(true)
    return(
        <Modal isOpen={open} toggle={() => setOpen(false)}>
            <ModalHeader>
                Cz E 
                <button  type="button" className="btn-close" onClick={() => setOpen(false)}  ></button>
            </ModalHeader>
            <ModalBody>
            <div>
            <h5>Maksymalna liczba punktw  40</h5>
            <h5>Czas przeznaczony na wykonanie zada - 15 min</h5>
            <h5>Warunkiem zdania egzaminu jest uzyskanie pozytywnego wyniku (minimum 60%, czyli 24p.) z kadej czci egzaminu</h5>
            <h4>Oglny opis umitnoci</h4>
            <p>Zdajcy na poziomie B1 powinni umie posugiwasi jzykiem polskim w typowych sytuacjach komunikacyjnych w sposb pynny, nieutrudniajcy komunikacji. Musz spenia wymagania zawarte w Standardach wymaga egzaminacyjnych".</p>
            <h4>Zdajcy powinni umie:</h4>
            <ul>
                <li>rozpoczyna i koczy rozmow, a take bra w niej udzia (wyraa wasne opinie, wymienia informacje na znane i zwizane z zainteresowaniami tematy);</li>
                <li>stosowa podstawowe zasady prowadzenia rozmw telefonicznych;</li>
                <li>tworzy proste, spjne wypowiedzi na temat wasnych zainteresowa, dowiadcze, marze i planw;</li>
                <li>wyraa i uzasadnia wasne pogldy;</li>
                <li>opisywa i relacjonowa zdarzenia oraz streszcza w prosty sposb przeczytany tekst;</li>
                <li>kontrolowa przebieg rozmowy, proszc, gdy zachodzi potrzeba, o powtrzenie, wyjanienie, przeliterowanie itp.;</li>
                <li>upewni si, czy s waciwie rozumiani i czy uywaj odpowiednich sw i form;</li>
                <li>operowa formami stylu oficjalnego i nieoficjalnego, odpowiednio do sytuacji;</li>
                <li>stosowa zasady wymowy i intonacji pozwalajce na waciwe zrozumienie wyraanych intencji oraz treci wypowiedzi (wpyw zasad wymowy jzyka rodzimego moe by odczuwalny, ale nie powinien zakca zrozumienia wypowiedzi).</li>
            </ul>
            <h4>Wskazwki dla przygotowujcych si do egzaminu</h4>
            <h5>Budowa egzaminu ustnego i typy zada</h5>
            <p>Komisja skada si zwykle z trzech osb, ale ze zdajcym rozmawia tylko jedna z nich, chocia w interakcj wcza si jeszcze jeden z egzaminatorw. Wypowiedzi oceniaj niezalenie od siebie dwie osoby. Oceniane s: sprawno komunikacyjna, wykonanie zadania, poprawno gramatyczna, sownictwo, wymowa i intonacja.</p>
            <p>Przed przystpieniem do egzaminu zdajcy moe przygotowa swoj wypowied  ma na to kilka minut. Na pocztku egzaminator zadaje kilka pyta rozgrzewajcych i sucych nawizaniu kontaktu. Dotyczy one mog np. zainteresowania jzykiem polskim czy wykonywanego zawodu. Ta cz wypowiedzi nie podlega ocenie. Egzamin ustny polega nawylosowaniu zestawu, w ktrym znajduj si trzy zadania (naley wykona wszystkie trzy w dowolnej kolejnoci):</p>
            <ol>
                <li>opis fotografii (sytuacji na niej przedstawionej);</li>
                <li>monolog (wypowied na podany temat);</li>
                <li>sytuacja komunikacyjna (dialog z egzaminatorem).</li>
            </ol>
        </div>
            </ModalBody>
        </Modal>
        
    )
    
}