import { useState } from "react"
import { Modal, ModalBody, ModalHeader } from "reactstrap"

export const DescCatB=()=>{
    const[open, setOpen]=useState(true)
    return(
        <Modal isOpen={open} toggle={() => setOpen(false)}>
            <ModalHeader>
                Cz B 
                <button  type="button" className="btn-close" onClick={() => setOpen(false)}  ></button>
            </ModalHeader>
            <ModalBody>
            <div>
            <h5>Maksymalna liczba punktw  40</h5>
            <h5>Czas przeznaczony na wykonanie zada - 60 min</h5>
            <h5>Warunkiem zdania egzaminu jest uzyskanie pozytywnego wyniku (minimum 60%, czyli 24p.) z kadej czci egzaminu</h5>
            <h4>Oglny opis umitnoci</h4>
            <p>Zdajcy powinni umie rozpoznawa i stosowa poprawne gramatycznie formy jzykowe, pozwalajce na wyraanie intencji mwicego w zakresie sprecyzowanym w standardach wymaga egzaminacyjnych. Bdy gramatyczne popeniane przez zdajcych w wypowiedziach mwionych i tekstach pisanych nie powinny uniemoliwia zrozumienia treci i intencji wypowiedzi.</p>
            <h4>Wskazwki dla przygotowujcych si do egzaminu</h4>
            <p>Sukces w tej czci egzaminu zaley od wczeniejszego przyswojenia wiedzy gramatycznej i wykonania odpowiedniej liczby wicze. Wan rzecz jest uporzdkowanie przed egzaminem (w formie tabel czy zestawie) trudniejszych zagadnie, np. rekcji czasownikw, wzorw tworzenia poszczeglnych form.</p>
            <p>Kade z zada bada inny problem gramatyczny, np.: stopniowanie przymiotnika, aspekt czasownika, odmian rzeczownika i przymiotnika. Zadania oparte s na krtkich tekstach, ktrych naley utworzy poprawn form gramatyczn wyrazw w nawiasie lub wybra spord podanych t prawidow. Mog pojawi si te zdania izolowane.</p>
            <p>Egzamin certyfikacyjny zawiera od 8 do 11 zada. Decyzja o kolejnoci wykonywania zada naley do zdajcego. Warto z tego skorzysta i na pocztku przejrze cay test, eby rozpocz od zada, ktre wydaj si najatwiejsze. Do tych sprawiajcych kopoty mona wrci na kocu.</p>
            <p>Jeli okae si, e jaka forma gramatyczna, ktra powinna pojawi si w danym kontekcie, stanowi trudno, nie naley si poddawa  lepiej wpisa cokolwiek, ni zostawi puste miejsce. Ta uwaga dotyczy wszystkich zada: za z odpowied nie traci si punktw. W miar moliwoci czasowych dobrze jest po rozwizaniu wszystkich zada przeczyta je jeszcze raz.</p>
            <h4>Uwaga! Zawsze naley uwanie przeczyta polecenie i przykad, poniewa tam znajduj si wane wskazwki!</h4>
        </div>
            </ModalBody>
        </Modal>
        
    )
    
}