import React from "react";
import { connect } from "react-redux";
import  Navbar  from "../components/Navbar";
import Statistic from "../components/statistic/Statistic";
import './home.css'

import grammar from '../assets/img/grammar.jpg'
import listening from '../assets/img/listening.jpg'
import reading from '../assets/img/reading.jpg'
import speaking from '../assets/img/speaking.jpg'
import writing from '../assets/img/writing.jpg'
import introduction from '../assets/img/introduction.jpg'
import { NavLink } from "react-router-dom";
import { clearStateA, clearStateB, clearStateC, clearStatistic } from "../store/actions/actionCreaters";

 const Home =(props)=>{
   
 return(
    <div className='home container-fluid'>
        
        <div className='row introdaction text-center image' style={{backgroundImage:`url(${introduction}) `}}>
        <Navbar></Navbar>
        {/* <div className='intMaska'></div> */}
                <div className=' intContainer'>
                    {/* <h2>Egzamin B1</h2> */}
                    <h3> Przykadowe testy z jzyka polskiego certyfikatowe dla poziomu B1</h3>
                    <p>Tu znajdziecie wszystkie typy zada obowizujce w testach certyfikatowych. Tre i forma wicze zostay podporzdkowane wykazowi funkcji i poj jzykowych, wykazowi tematycznemu i zagadnieniom gramatycznym, ktre zawarte s w standardach wymaga egzaminacyjnych dla poziomu B1.</p>
                    <p></p>
                </div>
               
        </div>
        <hr></hr>
        <div className='row cat catA'>
            <NavLink to="/CatA" className="nav-link col-md-6 left" onClick={props.clearStateA}>
            <div className='image' style={{backgroundImage:`url(${listening})`}}>
                <h2>Rozumienie ze suchu</h2>
                {/* <img src={reading}></img> */}
                <div className='imgDown'>
                    <h2>.<span>A</span></h2>
                </div>
            </div>
            </NavLink>
            <div className='col-md-6 right'>
                <p>
                Cz A zawiera wszystkie typy zada, ktre mog pojawi si w tecie egzaminacyjnym (luki do uzupenienia, prawda/fasz, wybr wielokrotny, dobieranie informacji, zadania z wykorzystaniem materiaw graficznych). Teksty do wicze na rozumienie ze suchu zostay nagrane.
                </p>
                <NavLink to="/CatA" className="nav-link" onClick={props.clearStateB}><button className='myBtn'>Rozpocznij</button></NavLink>
            </div>
        </div>
        <hr></hr>
        <div className='row cat catB'>
        <NavLink to="/CatB" className="nav-link col-md-6 left" onClick={props.clearStateB}>
            <div className='image' style={{backgroundImage:`url(${grammar})`}}>
                <h2>Poprawno gramatyczna</h2>
                <div className='imgDown'>
                    <h2>.<span>B</span></h2>
                </div>
            </div>
        </NavLink>
            <div className='col-md-6 right'>
                <p>
                wiczenia sprawdzajce umiejtnoci gramatyczne zostay zamieszczone w czci B. Znajdujsi tu zadania, w ktrych trzeba wybra poprawn form (wybr wielokrotny, wybr z ramki) lub j stworzy (zadaniaz lukami, transformacje).
                </p>
                <NavLink to="/CatB" className="nav-link" onClick={props.clearStateB}><button className='myBtn'>Rozpocznij</button></NavLink>
            </div>
        </div>
        <hr></hr>
        <div className='row cat catC'>
        <NavLink to="/CatC" className="nav-link col-md-6 left"   onClick={props.clearStateC}>
        <div className='image' style={{backgroundImage:`url(${reading})`}}>
            <h2>Rozumienie tekstw pisanych</h2>
            <div className='imgDown'>
                    <h2>.<span>C</span></h2>
                </div>
            </div>
        </NavLink>
            <div className='col-md-6 right'>
            <p>Cz C zawiera wszystkie typy zada testujcych kompetencj w zakresie rozumienia tekstw pisanych, ktre mog pojawi si podczas egzaminu (prawda/fasz, wybr wielokrotny, dobieranie informacji, zadania z lukami). Wybrane wiczenia to wzorcowe formy pisemne, ktre s wymagane na poziomie Bl (list nieoficjalny formalny, recenzja, podanie). Tym samym, sprawdzajc swoje umiejtnoci w zakresie rozumienia tekstu pisanego, mona przypomnie sobie reguy obowizujce przytworzeniu niektrych form pisemnych.</p>
            <NavLink to="/CatC" className="nav-link" onClick={props.clearStateC}><button className='myBtn'>Rozpocznij</button></NavLink>
            </div>
        </div>
        <hr></hr>
        <div className='row cat catD'>
        <NavLink to="/CatD" className="nav-link col-md-6 left" onClick={props.clearStatistic}>
        <div className='image' style={{backgroundImage:`url(${writing})`}}>
                <h2>Pisanie</h2>
                <div className='imgDown'>
                    <h2>.<span>D</span></h2>
                </div>
            </div>
        </NavLink>
            <div className='col-md-6 right'>
            <p>Cz D zawiera dwie wzorcowe formy pisemne (list oficjalny i list nieoficjalny) oraz propozycje tematw prac pisemnych w formie przykadowych zestaww egzaminacyjnych.</p>
            <NavLink to="/CatD" className="nav-link" onClick={props.clearStatistic}><button className='myBtn'>Rozpocznij</button></NavLink>
            </div>
        </div>
        <hr></hr>
        <div className='row cat catE'>
        <NavLink to="/CatE" className="nav-link col-md-6 left" onClick={props.clearStatistic}>
        <div className='image' style={{backgroundImage:`url(${speaking})`}}>
            <h2>Mwienie</h2>
            <div className='imgDown'>
                    <h2>.<span>E</span></h2>
                </div>
            </div>
        </NavLink>
            <div className='col-md-6 right'>
            <p>Cz E zawiera wzory wypowiedzi na wskazane tematy, w ktrych wytuszczono zdania i wyraenia pomocne przy tworzeniu samodzielnych wypowiedzi. Znale tu mona schemat opisu ilustracji, monolog, sytuacj komunikacyjn oraz zestawy przykadowych pyta egzaminacyjnych.</p>
            <NavLink to="/CatE" className="nav-link" onClick={props.clearStatistic}><button className='myBtn'>Rozpocznij</button></NavLink>
            </div>
        </div>
        <hr></hr>
        <div className='Statistic'>
        {
            (props.arrStatistic.length===0)?null:<Statistic></Statistic> 
        }
        </div>
    </div>
)}

const mapStateToProps=(state)=>{
    //console.log(state)
return(
    {
        arrStatistic:state.Statistic.arrStatistic
    }
)}
const mapDispatchToProps=(dispatch)=>(
    {
        clearStateA:()=>dispatch(clearStateA()),
        clearStateB:()=>dispatch(clearStateB()),
        clearStateC:()=>dispatch(clearStateC()),
        clearStatistic:()=>dispatch(clearStatistic())
    }
)

export default connect(mapStateToProps,mapDispatchToProps)(Home)