import { useState } from "react"
import { Modal, ModalBody, ModalHeader } from "reactstrap"

export const DescCatC=()=>{
    const[open, setOpen]=useState(true)
    return(
        <Modal isOpen={open} toggle={() => setOpen(false)}>
            <ModalHeader>
                Cz C 
                <button  type="button" className="btn-close" onClick={() => setOpen(false)}  ></button>
            </ModalHeader>
            <ModalBody>
            <div>
            <h5>Maksymalna liczba punktw  40</h5>
            <h5>Czas przeznaczony na wykonanie zada - 45 min</h5>
            <h5>Warunkiem zdania egzaminu jest uzyskanie pozytywnego wyniku (minimum 60%, czyli 24p.) z kadej czci egzaminu</h5>
            <h4>Oglny opis umitnoci</h4>
            <p>Zdajcy powinni rozumie pojedyncze napisyi ogoszenia, fragmenty tekstw dugoci jednego akapitu oraz kompletne krtkie teksty (160-250 sw) o tematyce zgodnej z wymaganiami zawartymi w Standardach wymaga egzaminacyjnych. Kandydaci powinni rozumie oglny sens tekstu oraz konkretnych informacji w nim zawartych.</p>
            <h4>Wskazwki dla przygotowujcych si do egzaminu</h4>
            <p>W tej czci, podobnie jak w czci A (rozumienieze suchu), oczekuje si od zdajcego globalnego i/lub szczegowego rozumienia tekstu  tym razem pisanego. Rnica polega na tym, e w czci C zdajcy moe wielokrotnie wraca do zadania. W odniesieniu do obydwu sprawnoci mona zastosowa podobne strategie  przede wszystkim podkrelanie istotnych informacji w tekcie. Jeli to nie jest konieczne, lepiej nie traci czasu na dokadn analiz caego tekstu i zastanawianie si nad znaczeniem kadego sowa, poniewa niezrozumienie pojedynczych sw zwykle nie utrudnia wykonania zadania. Naley skoncentrowa si na fragmentach wyznaczanych przez pytania lub na kontekcie wyrazw usunitych z tekstu. W tej czci powinno znale si nie wicej ni 5 zada. Najczciej spotykane rodzaje tekstw to: pojedyncze napisy, ogoszenia, fragmenty oryginalnych tekstw prasowych, listy prywatne i oficjalne, ulotki oraz notatki.</p>
            
        </div>
            </ModalBody>
        </Modal>
        
    )
    
}